.containers{
  border: 1px solid white;
  height: 100vh;
}

.container1{
  display: flex;
  flex-direction: column;
  margin: auto;
  margin-top: 200px;
  width: 600px;
  /* background: rgb(247,249,250); */
  padding-bottom: 30px;
  border: 1px solid rgb(247,249,250); ;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);
}

.headerad{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 9px;
  width: 100%;
  margin-top: 30px;
}

.textLogin {
  color: #9d3c00;
  font-size: 48px;
  font-weight: 700;
}

.underline{
  width: 61px;
  height: 6px;
  background: #9d3c00;
  border-radius: 9px;
}

.inputs{
  margin-top: 55px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.input1{
  display: flex;
  align-items: center;
  margin: auto;
  width: 480px;
  height: 80px;
  background: #eaeaea;
  border-radius: 6px;
}

.input1 img{
  margin: 0px 30px;
}

.input1 input{
  height: 50px;
  width: 400px;
  background: transparent;
  border: none;
  outline: none;
  color: #797979;
  font-size: 19px;
}

.input1 .password-viewer img{
  height: 24px;
  width: 24px;
}

.input1 .password-icon img{
  height: 21px;
  width: 19px;
}

.input1 .email-icon img{
  height: 16px;
  width: 21px;
}

.password-viewer{

  cursor: pointer;
}
