 /* Add or update styles for user type selection buttons  */
.user-type {
display: flex;
gap: 10px;
margin: auto;
}

.user-type-button {
background: #eaeaea;
border: none;
border-radius: 6px;
padding: 20px 45px;
cursor: pointer;
font-size: 19px;
}

.user-type-button.active {
background: #9d3c00;
color: #fff;
}

.user-type-button.active:hover {
  background: red;
  color: #fff;
}

